<template>
  <transition name="title">
    <section class="title-wrapper" v-show="ifTitleAndMenuShow">
      <div class="left">
        <span class="icon icon-back"></span>
      </div>
      <div class="right">
        <div class="icon-wrapper">
          <span class="icon icon-cart"></span>
        </div>
        <div class="icon-wrapper">
          <span class="icon icon-person"></span>
        </div>
        <div class="icon-wrapper">
          <span class="icon icon-more"></span>
        </div>
      </div>
    </section>
  </transition>
</template>

<script>
export default {
  props: {
    ifTitleAndMenuShow: {
      type: Boolean,
      default: false
    }
  }
};
</script>

<style scoped lang='scss'>
@import "@/assets/styles/global.scss";

// title 移入移出动画
.title-enter,
.title-leave-to {
  transform: translate3d(0, -100%, 0);
}
.title-enter-active,
.title-leave-active {
  transition: all 0.3s linear;
}
.title-enter-to,
.title-leave {
  transform: translate3d(0, 0, 0);
}
.title-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: px2rem(48);
  box-shadow: 0 px2rem(8) px2rem(8) rgba(0, 0, 0, 0.15);
  z-index: 101;
  display: flex;
  background-color: #fff;
  .left {
    flex: 0 0 px2rem(60);
    @include center;
  }
  .right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    .icon-wrapper {
      @include center;
      flex: 0 0 px2rem(40);
    }
  }
}
</style>


